import { memo } from 'react';

function SectionAbout({ className }) {
    return (
        <section className={["section", "section-padding", "section-about", className].join(' ')}>
            <div className="container">
                <div className="section-about__col-left">
                    <h2 className='section__title'> </h2>
                    <p>             . <strong>   ,</strong>              .</p>
                    <p> ,  ,        ()      ,   . <strong>   ,          </strong>    .</p>
                </div>
                <div className="section-about__col-right">
                    <img src={process.env.REACT_APP_PUBLIC_URL + "/section-about-img.png"} alt="" />
                </div>
            </div>
        </section>
    )
}

export default memo(SectionAbout);